import React from 'react';
import propTypes from 'prop-types';

const WeatherTable = (props) => {
    let tableBody = [];
    if (props.weatherForecast) {
        props
            .weatherForecast
            .forEach((element, i) => {
                tableBody.push(
                    <tr key={i}>
                        <td>{element.date}</td>
                        <td>
                            <img
                                className="fcst_wxicon"
                                alt=""
                                src={element.img
                                ? element.img
                                : ""}/>
                        </td>
                        <td>{element.rainyPercent}%</td>
                        <td>
                            <span
                                className={element.status === ''
                                ? 'tag is-danger is-medium'
                                : 'tag is-primary is-medium'}>
                                {element.status}
                            </span>
                        </td>
                    </tr>
                )
            })
    }

    return (
        <table className="table">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                {tableBody}
            </tbody>
        </table>
    )
}

WeatherTable.propType = {
    weatherForecast: propTypes.array.required
}

export default WeatherTable;



